<template>
  <div>
    <div class="cabecera">
      <h3 class="title-app">{{ msg }}</h3>
      <input v-model="dato" placeholder="¿Buscamos a alguien?" v-on:keyup.enter="search()" class="especial title-app">
      <b-button v-on:click="search()" variant="success">Buscar personaje</b-button>
    </div>
    <div class="cards container">
      <div class="contenedor" v-for="character of characters" :key="character.id">
        <div class="imagen">
          <img v-bind:src="character.image" alt=""/>
        </div>
        <div class="information">
          <h3>{{ character.name }}</h3>
          <p>{{ character.gender }}</p>
          <p>{{ character.species }}</p>
          <!--          <p>{{ character.type }}</p>-->
          <!--          <p>{{ character.status }}</p>-->
        </div>
      </div>
    </div>
    <nav>
      <b-button class="boton" variant="primary" v-on:click="changePage( page - 1)">Anterior</b-button>
      <b-button class="boton" variant="info">{{ page }}</b-button>
      <b-button class="boton" variant="primary" v-on:click="changePage( page + 1)">Siguiente</b-button>
    </nav>

  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "index",
  props: {
    msg: String,
  },
  data() {
    return {
      characters: [],
      page: 1,
      pages: 1,
      dato: ""
    };
  },
  methods: {
    fetch() {
      const params = {
        page: this.page,
        name: this.dato
      }
      axios.get("https://rickandmortyapi.com/api/character/", {params})
          .then(resp => {
            this.characters = resp.data.results
            this.pages = resp.data.info.pages
          })
          .catch(err => {
            console.log(err)
          })
    },
    changePage(page) {
      this.page = (page <= 0 || page > this.pages) ? this.page : page
      this.fetch()
    },
    search() {
      // this.page = 1
      this.fetch()
    }
  },
  mounted() {
    this.fetch()
  },
};
</script>
